import { useEffect, useState } from "react";
import axios from "axios";

const GOOGLE_API_KEY = "AIzaSyCU7Ys7zXQNREFO20MrQ5tWmEPXFrgK--A";
const SPREADSHEET_ID = "1A0gdvxzHv-WpfaYJvzaIpMmvxY46BDA0NE3T-A0IQ_8";

export const useStudents = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    const fetchData = async () => {
      const range = "students!A2:k200"; // Adjust the range to the data you need

      const url = `https://sheets.googleapis.com/v4/spreadsheets/${SPREADSHEET_ID}/values/${range}?key=${GOOGLE_API_KEY}`;

      try {
        const response = await axios.get(url);
        const students = response.data.values.map((row) => ({
          name: row[0],
          level: row[1],
          supervisor: row[2],
          phone: row[3],
          lastPage: row[4],
          reviewedPages: row[5],
          firstTest: row[6],
          secondTest: row[7],
          thirdTest: row[8],
          fourthTest: row[9],
          fifthTest: row[10],
        }));
        setData(students);
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return {
    data,
    loading,
  };
};
