import { Autocomplete, Stack, TextField } from "@mui/material";
import React from "react";

export default function SearchPage() {
  return (
    <Stack>
      <Autocomplete
        dir="rtl"
        disablePortal
        options={[]}
        sx={{ width: "100%" }}
        renderInput={(params) => (
          <TextField {...params} label="  " variant="standard" />
        )}
      />
    </Stack>
  );
}
